body {
  margin: 0;
}

.search-form {
  z-index: 1;
  width: 100%;
  height: 80px;
  background-color: rgba(92, 89, 191, .7);
  justify-content: center;
  align-items: center;
  display: flex;
  position: fixed;
}

.input {
  width: 260px;
  height: 30px;
  border: none;
  border-radius: 10px;
  margin-right: 20px;
  padding: 0 15px;
  font-size: 17px;
}

.input:hover, .input:focus {
  outline-color: #7fd2e5;
}

.button {
  width: 120px;
  height: 30px;
  text-transform: uppercase;
  color: #7fd2e5;
  text-shadow: 1px 0 1px 2px #4a4a4a;
  border: 1px solid #909090;
  border-radius: 10px;
  font-size: 15px;
  font-weight: 600;
  box-shadow: inset 3px 3px 6px #ccdbe8, inset -3px -3px 6px 1px rgba(255, 255, 255, .5);
}

.gallery {
  flex-wrap: wrap;
  justify-content: space-around;
  padding-top: 80px;
  display: flex;
}

.photo-card {
  width: 33.3333%;
}

img {
  object-fit: cover;
  width: 100%;
  height: 100%;
  transition: transform .2s ease-in-out;
  display: block;
}

a:hover img, a:focus img {
  border-radius: 10px;
  transform: scale(1.15);
}

